body{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
.div_container{
  border: 2px solid red;
  display: flex;
  
 
}
.main{
  border: 2px solid rgb(13, 26, 214);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
}

.container{
  border: 2px solid rgb(212, 0, 255);
  display: flex;
  
  flex-direction: column;
  width: 500px;
  height: 600px;
  margin: 30px;
}
.container img{
  width: 100%;
  border: 2px solid red;
  height: 50%;

}
